import React, { useEffect, useState } from "react";
import axios from "axios";
import Swal from "sweetalert2";
import { Button } from "../styles";
import { Link, withRouter } from "react-router-dom";
import AuthService from "../services/auth";

function DBReading(props) {
  const [userData, setUser] = useState([]);
  const [readings, setId] = useState([]);
  const [cards, setCard] = useState([]);
  const isProduction = process.env.NODE_ENV === "production";
  const baseURL = isProduction
    ? "https://fideliuss-app.herokuapp.com"
    : "http://localhost:3000";
  const id = props.match.params.id;
  useEffect(() => {
    const authService = new AuthService();
    authService.profile().then(res => {
      const userData = res.data.user;
      setUser(userData);
      const readings = res.data.user.readings[0];
      setId(readings);

      const cards = readings.cards;
      setCard(cards);
      console.log(cards);
    });
  }, [baseURL]);

  const deleteReading = () => {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      type: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes"
    }).then(res => {
      if (res.value) {
        axios
          .delete(`http:localhost:3000/reading/${props.match.params.id}`)
          .then(({ data }) => {
            Swal.fire("Deleted!", data.msg, "success");
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        return;
      }
    });
  };

  return (
    <div>
      {cards.map((card, i) => {
        return (
          <div key={i}>
            {!card ? (
              <p>Loading</p>
            ) : (
              <div key={i}>
                <p>{card.name}</p>
                <img src={card.img} alt="card" />
                <p>{card.meaning_up}</p>
                <p>{card.desc}</p>
              </div>
            )}
          </div>
        );
      })}
      <Link to="/profile">
        <Button onClick={deleteReading}>Delete</Button>
      </Link>
    </div>
  );
}

export default withRouter(DBReading);
