const Signup = props => {
  const { changeIsLogged, changePlace } = useContext(MyContext);
  const authService = new AuthService();
  const [form, handleInputs] = useForm();

  useEffect(() => {
    changePlace("auth");
  }, [changePlace]);

  const handleSignup = () => {
    authService
      .signup(form)
      .then(response => {
        changeIsLogged(true);
        localStorage.setItem("USER", JSON.stringify(response.data.user));
        props.history.push("/profile");
      })
      .catch(err => {
        console.log(err);
      });
  };

  return (
    <div className="auth">
      <h2>Signup</h2>

      <input
        type="text"
        name="name"
        placeholder="Name"
        onChange={e => handleInputs(e)}
      />
      <input
        type="email"
        name="email"
        placeholder="e-mail"
        onChange={e => handleInputs(e)}
      />
      <input
        type="password"
        name="password"
        placeholder="password"
        onChange={e => handleInputs(e)}
      />
      <Button onClick={handleSignup}>Sign up</Button>
    </div>
  );
};

export default withRouter(Signup);
