import React, { useEffect, useState } from "react";
import axios from "axios";
import useForm from "../hooks/useForm";
import { Form, Button } from "../styles";
import AuthService from "../services/auth";
import { Link, withRouter } from "react-router-dom";

const SixCards = props => {
  const [cards, setCard] = useState([]);
  const [user, setUser] = useState({});
  const [form, handleInputs] = useForm();
  const isProduction = process.env.NODE_ENV === "production";
  const baseURL = isProduction
    ? "https://fideliuss-app.herokuapp.com"
    : "http://localhost:3000";
  useEffect(() => {
    axios
      .get(`${baseURL}/cards/random?n=6`)
      .then(({ data }) => {
        setCard(prevState => {
          return [...prevState, ...data.cards];
        });
      })
      .catch(err => {
        console.log(err, "iiuiu");
      });
    const authService = new AuthService();
    authService.profile().then(res => {
      const userData = res.data.user;
      setUser(userData);
    });
  }, [baseURL]);

  const saveReading = () => {
    axios
      .post(`${baseURL}/savereading`, {
        ...form,
        cards,
        user
      })
      .then(({ data }) => {
        return data.name, props.history.push("/profile");
      })
      .catch(err => {
        console.log(err);
      });
  };

  return (
    <div>
      <h1>Six Cards Spread</h1>

      {cards.map((card, i) => {
        return (
          <div key={i}>
            {!card ? (
              <p>Loading</p>
            ) : (
              <div key={i}>
                <p>{card.name}</p>
                <img src={card.img} alt="card" />
                <p>{card.meaning_up}</p>
                <p>{card.desc}</p>
              </div>
            )}
          </div>
        );
      })}
      <Form onSubmit={saveReading}>
        <input
          type="text"
          name="name"
          placeholder="name your reading"
          onChange={e => handleInputs(e)}
        />

        <Button>Save Reading</Button>
      </Form>
    </div>
  );
};

export default withRouter(SixCards);
